<template>
    <div class="text-center">
        <v-dialog
            v-model="dialog"
            max-width="500"
            @click:outside="changeDialogLogin(false)"
            @keydown.esc="changeDialogLogin(false)"
        >
            <v-card class="pa-10">
                <div class="d-flex justify-center">
                    <span class="text-uppercase font-weight-bold text-h4 mb-4"> login </span>
                </div>

                <div class="">
                    <v-form ref="form" v-model="valid" class="">
                        <v-text-field
                            v-model="email"
                            :rules="emailRules"
                            label="E-mail"
                            color="#6755e7"
                            name="email"
                            outlined
                            required
                            full-width
                        ></v-text-field>
                        <v-text-field
                            v-model="password"
                            :rules="passwordRules"
                            label="Password"
                            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                            :type="showPassword ? 'text' : 'password'"
                            name="password"
                            outlined
                            required
                            @click:append="showPassword = !showPassword"
                        ></v-text-field>
                        <v-btn
                            class="my-2"
                            color="#6755e7"
                            x-large
                            dark
                            block
                            @click.prevent="submitLogin"
                        >
                            login
                        </v-btn>
                    </v-form>
                </div>

                <v-card-actions>
                    <v-spacer></v-spacer>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import firebase from "firebase";
import { mapGetters, mapActions } from "vuex";
export default {
    name: "LoginDialog",

    data() {
        return {
            email: "userapplanding@mail.com",
            password: "password",
            showPassword: false,
            emailRules: [
                (v) => !!v || "E-mail is required",
                (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
            ],
            passwordRules: [
                (v) => !!v || "Password is required",
                // v => (v && v.length >= 20) || 'Title must be less than 20 characters',
            ],
            valid: true,
            loginBtn: [
                {
                    name: "google",
                    icon: "mdi-google",
                    color: "#fff",
                    iconColor: "#000",
                    link: this.loginWithGoogle,
                },
                {
                    name: "twiter",
                    icon: "mdi-twitter",
                    color: "#009deb",
                    iconColor: "#fff",
                    link: this.loginWithGoogle,
                },
                {
                    name: "facebook",
                    icon: "mdi-facebook",
                    color: "#385498",
                    iconColor: "#fff",
                    link: this.loginWithGoogle,
                },
                {
                    name: "github",
                    icon: "mdi-github",
                    color: "#000",
                    iconColor: "#fff",
                    link: this.loginWithGoogle,
                },
            ],
        };
    },

    computed: {
        ...mapGetters({
            dialog: "getDialogLogin",
        }),
    },

    methods: {
        ...mapActions({
            changeDialogLogin: "updateDialogLogin",
            getCurrentUser: "getCurrentUser",
        }),
        submitLogin() {
            firebase
                .auth()
                .signInWithEmailAndPassword(this.email, this.password)
                .then((response) => {
                    alert(`You are logged in as ${response.email}`);
                    this.$router.go({ path: this.$router.path });
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        loginWithGoogle() {
            const provider = new firebase.auth.GoogleAuthProvider();
            firebase
                .auth()
                .signInWithPopup(provider)
                .then((result) => {
                    console.log(result);
                    const data = {
                        currentUser: result,
                        isLogin: true,
                    };
                    this.changeIsLogin(data);
                    alert(`You are logged in as ${result.email}`);
                    this.$router.go({ path: this.$router.path });
                    // This gives you a Google Access Token. You can use it to access the Google API.
                    // const token = result.credential.accessToken;
                    // The signed-in user info.
                    // const user = result.user;
                })
                .catch((err) => {
                    console.log(err);
                    // Handle Errors here.
                    // const errorCode = error.code;
                    // const errorMessage = error.message;
                    // The email of the user's account used.
                    // const email = error.email;
                    // The firebase.auth.AuthCredential type that was used.
                    // const credential = error.credential;
                });

            console.log("test");
        },
    },
};
</script>

