<template>
    <div class="bg-icon d-flex flex-wrap justify-center align-center">
        <v-icon
            v-for="(item, i) in bgIcon"
            :key="'a' + i"
            size="50"
            class="bg-icon-item ma-8"
            color="#1b1b1b5e"
            :style="{ 
                'animation-delay': delayRandom[i]+'s',
                'animation-duration': 80+'s',
            }"
        >
            {{ item }}
        </v-icon>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
    name: "Home",

    components: {
        //
    },

    mounted () {
        this.makeRandomDelay(this.bgIcon.length);
    },

    computed: {
        ...mapGetters({
            bgIcon: "getBgIcon",
        }),
    },

    data: () => ({
        delayRandom: "",
    }),

    methods: {
        consoleLogArray() {
            console.log(this.delayRandom);
        },
        makeRandomDelay(n) {
            let arrayNotRandom = this.makeArray(n);
            this.delayRandom = this.shuffle(arrayNotRandom);
        },
        makeArray(n) {
            let a = [];
            for (var i = 1; i <= n; i++) {
                a.push(i);
            }
            return a;
        },
        shuffle(array) {
            // membuat acak penerima kartu
            // ----------------------------------------------------------------------------
            var currentIndex = array.length,
                temporaryValue,
                randomIndex;

            // While there remain elements to shuffle...
            while (0 !== currentIndex) {
                // Pick a remaining element...
                randomIndex = Math.floor(Math.random() * currentIndex);
                currentIndex -= 1;

                // And swap it with the current element.
                temporaryValue = array[currentIndex];
                array[currentIndex] = array[randomIndex];
                array[randomIndex] = temporaryValue;
            }

            return array;
        },
    },
};
</script>

<style scoped>
.bg-icon {
    position: absolute;
    white-space: nowrap;
}
/* .bg-icon .bg-icon-item:hover {
    text-shadow: 0 0 120px white;
} */
.bg-icon .bg-icon-item {
    color: #2423235e;
    /* text-shadow: 0 0 120px #2423235e; */
}
.bg-icon .bg-icon-item {
    animation-name: animation;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    /* animation: animation 3s linear 1; */
}
@keyframes animation {
    37.5%, 100% {
        text-shadow: none;
    }

    0% {
        text-shadow: none;
    }

    10% {
        text-shadow: 0 0 100px crimson;
    }
}
</style>