<template>
    <div class="text-center">
        <v-dialog
            v-model="dialog"
            max-width="800"
            @click:outside="changeDialogContact(false)"
            @keydown.esc="changeDialogContact(false)"
        >
            <v-card class="pa-10">
                <div class="d-flex justify-center">
                    <span class="text-uppercase font-weight-bold text-h5 mb-4">
                        contact me
                    </span>
                </div>

                <div class="">
                    <v-form ref="form" v-model="valid" class="">
                        <v-row>
                            <v-col cols="12" md="6" class="py-0">
                                <v-text-field
                                    v-model="mailData.name"
                                    :rules="nameRules"
                                    label="Name"
                                    color="#6755e7"
                                    solo
                                    required
                                ></v-text-field>
                            </v-col>
                            <v-col cols="12" md="6" class="py-0">
                                <v-text-field
                                    v-model="mailData.email"
                                    :rules="emailRules"
                                    label="Email"
                                    color="#6755e7"
                                    solo
                                    required
                                ></v-text-field>
                            </v-col>
                            <v-col cols="12" class="py-0">
                                <v-text-field
                                    v-model="mailData.title"
                                    :rules="titleRules"
                                    label="Title"
                                    color="#6755e7"
                                    solo
                                    required
                                ></v-text-field>
                            </v-col>
                            <v-col cols="12" class="py-0">
                                <v-textarea
                                    v-model="mailData.message"
                                    :rules="messageRules"
                                    solo
                                    name="input-7-4"
                                    label="Message"
                                ></v-textarea>
                            </v-col>
                        </v-row>

                        <v-btn
                            class="my-2"
                            color="#6755e7"
                            x-large
                            dark
                            block
                            @click="addData"
                        >
                            send
                        </v-btn>
                    </v-form>
                </div>

                <v-card-actions>
                    <v-spacer></v-spacer>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import slug from "slug";
import { db, fb } from "@/components/firebaseInit";
import { mapGetters, mapActions } from "vuex";
export default {
    name: "ContactDialog",

    data() {
        return {
            valid: true,
            name: "",
            nameRules: [
                (v) => !!v || "Name is required",
                (v) =>
                    (v && v.length <= 20) ||
                    "Name must be less than 20 characters",
            ],
            email: "",
            emailRules: [
                (v) => !!v || "E-mail is required",
                (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
            ],
            title: "",
            titleRules: [(v) => !!v || "Message is required"],
            message: "",
            messageRules: [(v) => !!v || "Message is required"],
            mailData: {
                email: "zombie@mail.com",
                name: "Zombie",
                title: "Plant vs Zombie",
                message: "Zombie ipsum reversus ab viral inferno, nam rick grimes malum cerebro. De carne lumbering animata corpora quaeritis. Summus brains sit​​, morbo vel maleficia? De apocalypsi gorger omero undead survivor dictum mauris. Hi mindless mortuis soulless creaturas, imo evil stalking monstra adventus resi dentevil vultus comedat cerebella viventium. Qui animated corpse, cricket bat max brucks terribilem incessu zomby. The voodoo sacerdos flesh eater, suscitat mortuos comedere carnem virus. Zonbi tattered for solum oculi eorum defunctis go lum cerebro. Nescio brains an Undead zombies. Sicut malus putrid voodoo horror. Nigh tofth eliv ingdead.",
                slug: "",
            },
        };
    },

    computed: {
        ...mapGetters({
            dialog: "getDialogContact",
        }),
    },

    methods: {
        ...mapActions({
            changeDialogContact: "updateDialogContact",
        }),
        validate() {
            return this.$refs.form.validate();
            // this.$refs.form.reset();
            // this.$refs.form.resetValidation()
        },
        async addData() {
            if (this.validate() == true) {
                await this.slugMail(5);
                await this.timeStamp();
                db.collection("inbox_mail")
                    .add(this.mailData)
                    .then((docRef) => {
                        console.log("Document written with ID: ", docRef.id);
                    })
                    .catch((error) => {
                        console.error("Error adding document: ", error);
                    });
                this.changeDialogContact(false);
            } else {
                alert("Form salah");
            }
        },
        async slugMail(q) {
            let text = " ";
            let chars = "abcdefghijklmnopqrstuvwxyz1234567890";

            for (let i = 0; i < q; i++) {
                text += chars.charAt(Math.floor(Math.random() * chars.length));
            }
            const aslug = this.mailData.title + text;
            this.mailData.slug = slug(aslug);
        },
        async timeStamp() {
            this.mailData.created_at = fb.firestore.FieldValue.serverTimestamp();
        },
    },
};
</script>

<style scoped>
</style>