<template>
    <div class="">
        <transition name="fade" mode="out-in" class="d-flex">
            <div
                class="floating-menu rounded-lg pa-4"
                v-show="colorPickerStatus"
            >
                <!-- background color -->
                <div class="d-flex justify-space-between mb-3">
                    <span class="text-capitalize font-weight-bold">
                        background color
                    </span>
                    <span>
                        {{ colorPicker }}
                    </span>
                </div>
                <v-color-picker
                    v-model="colorPicker"
                    dot-size="25"
                    mode="hexa"
                    swatches-max-height="200"
                ></v-color-picker>
                <v-btn
                    dark
                    class="float-right"
                    color="#3f51b5"
                    @click="changePrimaryColor(colorPicker)"
                >
                    save
                </v-btn>
            </div>
        </transition>
        <div class="floating-btn">
            <v-btn
                fab
                color="white"
                class=""
                @click="colorPickerStatus = !colorPickerStatus"
            >
                <v-icon class="mdi-spin"> mdi-cog </v-icon>
            </v-btn>
        </div>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
    name: "OptionBtnPorto",

    mounted () {
        this.colorPicker = this.primaryColor;
    },

    data() {
        return {
            colorPicker: "#e93e8c",
            colorPickerStatus: false,
        };
    },

    computed: {
        ...mapGetters({
            primaryColor: "getPrimaryColor",
        }),
    },

    methods: {
        ...mapActions({
            changePrimaryColor: "setPrimaryColor",
        }),
    },
};
</script>

<style scoped>
.floating-menu {
    position: fixed;
    bottom: 15%;
    right: 1%;
    width: 300px;
    background-color: white;
    z-index: 100;
}
.floating-btn {
    position: fixed;
    bottom: 2%;
    right: 1%;
    z-index: 100;
}
</style>