<template>
    <div>
        <v-app-bar fixed :color="primaryColor" elevate-on-scroll height="80">
            <v-container class="d-flex justify-space-between align-center">
                <v-app-bar-nav-icon
                    class="hidden-lg-and-up"
                    @click.stop="drawer = !drawer"
                ></v-app-bar-nav-icon>

                <p
                    class="hidden-md-and-down text-white text-h4 text-capitalize font-weight-bold"
                >
                    jhon doe
                </p>
                <v-spacer></v-spacer>
                <scrollactive
                    active-class="active"
                    :offset="80"
                    :duration="800"
                    :modify-url="false"
                    bezier-easing-value=".5,0,.35,1"
                    class="hidden-md-and-down"
                >
                    <span
                        v-for="(q, i) in navData"
                        :key="i"
                        :data-section-selector="q.link"
                        @itemchanged="onItemChanged"
                        class="navbar-portofolio text-white scrollactive-item text-uppercase font-weight-bold mx-3"
                    >
                        {{ q.title }}
                    </span>
                </scrollactive>
            </v-container>
        </v-app-bar>
        <v-navigation-drawer v-model="drawer" width="300" fixed>
            <v-list-item>
                <v-list-item-content>
                    <v-list-item-title
                        class="text-h5 text-uppercase font-weight-bold"
                    >
                        jhon doe
                    </v-list-item-title>
                    <v-list-item-subtitle class="text-capitalize">
                        coding with art
                    </v-list-item-subtitle>
                </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>

            <v-list dense nav>
                <v-list-item
                    v-for="item in navData"
                    :key="item.title"
                    link
                    @click="$vuetify.goTo(item.link, options)"
                >
                    <v-list-item-icon>
                        <v-icon>{{ item.icon }}</v-icon>
                    </v-list-item-icon>

                    <v-list-item-content>
                        <v-list-item-title class="text-uppercase">{{
                            item.title
                        }}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
import Scrollactive from "vue-scrollactive/src/scrollactive.vue";
export default {
    name: "NavBar",

    components: {
        Scrollactive,
    },

    data() {
        return {
            duration: 1000,
            offset: 0,
            easing: "easeInOutCubic",
            drawer: false,
        };
    },

    computed: {
        ...mapGetters({
            primaryColor: "getPrimaryColor",
            navData: "getNavData"
        }),
        options() {
            return {
                duration: this.duration,
                offset: this.offset,
                easing: this.easing,
            };
        },
    },

    methods: {
        onItemChanged(event, currentItem, lastActiveItem) {
            console.log("tes");
            console.log(
                "🚀 ~ file: NavBar.vue ~ line 79 ~ onItemChanged ~ event",
                event
            );
            console.log(
                "🚀 ~ file: NavBar.vue ~ line 79 ~ onItemChanged ~ currentItem",
                currentItem
            );
            console.log(
                "🚀 ~ file: NavBar.vue ~ line 79 ~ onItemChanged ~ lastActiveItem",
                lastActiveItem
            );
            // here you have access to everything you need regarding that event
        },
    },
};
</script>

<style scoped>
.navbar-portofolio {
    size: 2em;
    transition: .5s;
}
.navbar-portofolio:hover {
    cursor: pointer;
}
.active {
    color: black;
}
</style>