<template>
    <div
        id="our-work"
        class="base-page d-flex justify-center"
        v-resize="onResize"
    >
        <v-container
            class="d-flex flex-column justify-center align-center my-6"
        >
            <div class="d-flex flex-column justify-center align-center">
                <p
                    class="portofolio-one-page-text-color-3 portofolio-one-page-title-section text-h3 text-capitalize font-weight-bold"
                >
                    our best work
                </p>
                <p class="portofolio-one-page-text-color-2 body-1 text-center">
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Eum
                    ut animi, eius totam praesentium recusandae <br />
                    aut at architecto officia sed sit dolor consequatur.
                </p>
            </div>
            <div class="">
                <v-tabs
                    v-model="tab"
                    color="#31424b"
                    background-color=""
                    centered
                    light
                    icons-and-text
                >
                    <v-tabs-slider></v-tabs-slider>

                    <v-tab href="#tab-1"> Recents </v-tab>

                    <v-tab href="#tab-2"> Favorites </v-tab>

                    <v-tab href="#tab-3"> Nearby </v-tab>
                </v-tabs>

                <v-tabs-items v-model="tab">
                    <v-tab-item v-for="i in 3" :key="i" :value="'tab-' + i">
                        <v-card flat>
                            <v-row
                                class="d-flex justify-md-start justify-center align-center"
                            >
                                <v-col
                                    cols="10"
                                    sm="10"
                                    md="4"
                                    v-for="a in 4"
                                    :key="a"
                                >
                                    <div class="grey rounded-lg">
                                        <v-img
                                            :src="
                                                require('../../assets/dummy/default-image.jpg')
                                            "
											:width="aspectRatio"
                                            eager
                                        >
                                        </v-img>
                                    </div>
                                </v-col>
                            </v-row>
                        </v-card>
                    </v-tab-item>
                </v-tabs-items>
            </div>
        </v-container>
    </div>
</template>

<script>
export default {
    name: "OurWork",

    mounted() {
        this.onResize();
    },

    data() {
        return {
            tab: null,
            text:
                "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.",
            windowSize: {
                x: 0,
                y: 0,
            },
			aspectRatio: undefined,
        };
    },

	watch: {
		'windowSize.x' (newValue) {
			this.aspectRatio = newValue >= 830 ? undefined : newValue - 25
		}
	},

    methods: {
        onResize() {
            this.windowSize = { x: window.innerWidth, y: window.innerHeight };
        },
    },
};
</script>

