<template>
    <div>
        <v-sheet
            rounded
            min-height="460"
            color="transparent"
            class="d-flex flex-column"
        >
            <v-pagination
                class="align-self-end mb-2"
                v-model="page"
                :length="Math.ceil(mail.length / perPage)"
            ></v-pagination>
            <v-list two-line>
                <v-list-item-group
                    v-model="selected"
                    active-class="pink--text"
                    multiple
                    class=""
                >
                    <template v-for="(item, index) in visiblePages">
                        <v-list-item
                            :key="item.title + index"
                            @dblclick="readMail"
                        >
                            <template v-slot:default="{ active }">
                                <v-list-item-content>
                                    <v-list-item-title
                                        v-text="item.title"
                                    ></v-list-item-title>

                                    <v-list-item-subtitle
                                        class="text--primary"
                                        v-text="item.headline"
                                    ></v-list-item-subtitle>

                                    <v-list-item-subtitle
                                        v-text="item.subtitle"
                                    ></v-list-item-subtitle>
                                </v-list-item-content>

                                <v-list-item-action>
                                    <v-list-item-action-text
                                        v-text="item.action"
                                    ></v-list-item-action-text>

                                    <v-icon
                                        v-if="!active"
                                        color="grey lighten-1"
                                    >
                                        mdi-star-outline
                                    </v-icon>

                                    <v-icon v-else color="yellow darken-3">
                                        mdi-star
                                    </v-icon>
                                </v-list-item-action>
                            </template>
                        </v-list-item>

                        <v-divider
                            v-if="index < visiblePages.length - 1"
                            :key="index"
                        ></v-divider>
                    </template>
                </v-list-item-group>
            </v-list>
        </v-sheet>
    </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
    name: "ListMail",

    data() {
        return {
            selected: [],
            page: 1,
            perPage: 5,
        };
    },

    computed: {
        ...mapGetters({
            mail: "getMail",
            resetPage: "getResetPage",
        }),
        visiblePages() {
            return this.mail.slice(
                (this.page - 1) * this.perPage,
                this.page * this.perPage
            );
        },
    },

    watch: {
        resetPage(newValue, oldValue) {
            if (newValue != oldValue) {
                this.resetPageTo();
            }
            console.log(newValue + " dari list mail");
        },
    },

    methods: {
        ...mapActions({
            setCurrentPage: "setCurrentPage",
        }),
        readMail() {
            this.$router.push({ name: "ReadMail" });
        },
        resetPageTo() {
            this.page = 1;
            console.log("page = 1");
        },
        changeCurrentPage() {
            this.setCurrentPage(this.page);
        },
    },
};
</script>

<style lang="scss" scoped>
</style>