<template>
    <div id="why" class="base-page">
        <v-container
            class="d-flex flex-column justify-center align-center my-6"
        >
            <div class="d-flex flex-column justify-center align-center">
                <p
                    class="portofolio-one-page-text-color-3 portofolio-one-page-title-section text-h3 text-capitalize font-weight-bold text-center"
                >
                    why i'm different ?
                </p>
                <p
                    class="portofolio-one-page-text-color-2 text-center text-body-1"
                >
                    Lorem ipsum dolor sit amet consectetur adipisicing elit.
                    Recusandae, odio eligendi?
                </p>
            </div>
            <div class="">
                <v-row class="d-flex justify-center align-center">
                    <v-col cols="10" sm="5" md="4" v-for="(n, i) in whyData" :key="i">
                        <div
                            class="our-service-card d-flex flex-sm-column flex-lg-row justify-center rounded-lg pa-2"
                        >
                            <div
                                class="circle d-none d-md-flex justify-center align-center"
                            >
                                <v-icon size="50" :color="primaryColor">
                                    {{ n.icon }}
                                </v-icon>
                            </div>
                            <div class="ml-6">
                                <p
                                    class="portofolio-one-page-text-color-3 text-center text-lg-left text-capitalize font-weight-bold text-h6"
                                >
                                    {{ n.name }}
                                </p>
                                <div
                                    class="d-flex d-md-none justify-center align-center"
                                >
                                    <v-icon size="50" color="#e93e8c">
                                        {{ n.icon }}
                                    </v-icon>
                                </div>
                                <p
                                    class="portofolio-one-page-text-color-2 text-center text-lg-left body-1"
                                >
                                    {{ n.descript }}
                                </p>
                            </div>
                        </div>
                    </v-col>
                    <v-col
                        cols="12"
                        md="6"
                        class="d-flex justify-center align-center"
                    >
                        <v-img
                            :src="
                                require('../../assets/dummy/default-image.jpg')
                            "
                            :width="aspectRatio"
                            eager
                        >
                        </v-img>
                    </v-col>
                    <v-col cols="10" sm="10" md="6">
                        <div class="">
                            <p
                                class="portofolio-one-page-text-color-3 text-capitalize text-h6 font-weight-bold"
                            >
                                my skill
                            </p>
                            <p class="portofolio-one-page-text-color-2 body-1">
                                Lorem ipsum dolor sit amet consectetur
                                adipisicing elit. Vel veritatis repudiandae
                                fugiat, corrupti esse quaerat.
                            </p>
                            <div>
                                <div
                                    class=""
                                    v-for="(q, i) in skillData"
                                    :key="i"
                                >
                                    <span
                                        class="text-capitalize body-1 font-weight-bold"
                                    >
                                        {{ q.name }}
                                    </span>
                                    <v-progress-linear
                                        v-model="q.value"
                                        :color="primaryColor"
                                        height="20"
                                        class="my-2 rounded-lg"
                                    >
                                        <template v-slot:default="{ value }">
                                            <strong
                                                >{{ Math.ceil(value) }}%</strong
                                            >
                                        </template></v-progress-linear
                                    >
                                </div>
                            </div>
                        </div>
                    </v-col>
                </v-row>
            </div>
        </v-container>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
    name: "Why",
    mounted() {
        this.onResize();
    },
    
    data() {
        return {
            windowSize: {
                x: 0,
                y: 0,
            },
            aspectRatio: undefined,
        };
    },

    computed: {
        ...mapGetters({
            whyData: "getWhyData",
            skillData: "getSkillData",
            primaryColor: "getPrimaryColor",
        }),
    },

    watch: {
        "windowSize.x"(newValue) {
            this.aspectRatio = newValue >= 830 ? undefined : newValue - 25;
        },
    },

    methods: {
        onResize() {
            this.windowSize = { x: window.innerWidth, y: window.innerHeight };
        },
    },
};
</script>

<style scoped>
.circle {
    height: 70px;
    min-width: 70px;
    background-color: white;
    border: 1px solid #e9eaec;
    border-radius: 50%;
}
</style>
