<template>
    <div class="">
        <v-app-bar
            fixed
            dark
            elevate-on-scroll
            height="70"
            class=""
            color="transparent"
            id="nav-bar"
            :class="appClass"
            v-scroll="onScroll"
        >
            <v-container>
                <v-row class="d-flex d-md-none">
                    <v-col cols="1">
                        <v-app-bar-nav-icon
                            @click.stop="drawer = !drawer"
                        ></v-app-bar-nav-icon>
                    </v-col>
                    <v-col cols="11" class="d-none d-sm-flex justify-center">
                        <p class="nav-title mb-0">chat app</p>
                    </v-col>
                </v-row>
                <v-row class="hidden-sm-and-down">
                    <v-col cols="4">
                        <p class="nav-title mb-0">chat app</p>
                    </v-col>
                    <v-col cols="8" class="d-flex justify-end align-center">
                        <div class="d-flex justify-end align-center">
                            <scrollactive
                                active-class="active"
                                :offset="50"
                                :duration="800"
                                :modify-url="false"
                                bezier-easing-value=".5,0,.35,1"
                                class="d-md-flex"
                            >
                                <span
                                    v-for="(q, i) in menuSingel"
                                    :key="i"
                                    :data-section-selector="q.link"
                                    @itemchanged="onItemChanged"
                                    class="nav-menu scrollactive-item text-uppercase mx-4"
                                >
                                    {{ q.title }}
                                </span>
                            </scrollactive>
                            <v-menu
                                :open-on-hover="false"
                                nudge-bottom="20"
                                offset-y
                                transition="slide-y-reverse-transition"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                    <v-btn
                                        class="mx-2"
                                        color="white"
                                        elevation="0"
                                        outlined
                                        dark
                                        v-bind="attrs"
                                        v-on="on"
                                    >
                                        <span class="nav-menu"> Account </span>
                                    </v-btn>
                                </template>
                                <v-list>
                                    <v-list-item
                                        v-for="(sub, index) in menuLoginExpand"
                                        :key="index"
                                    >
                                        <v-list-item-title
                                            class="d-flex align-center"
                                        >
                                            <v-icon class="mr-4">
                                                {{ sub.icon }}
                                            </v-icon>
                                            <span
                                                class="text-uppercase font-weight-bold"
                                            >
                                                {{ sub.title }}
                                            </span>
                                        </v-list-item-title>
                                    </v-list-item>
                                </v-list>
                            </v-menu>
                        </div>
                    </v-col>
                </v-row>
            </v-container>
        </v-app-bar>
        <v-navigation-drawer v-model="drawer" width="300" fixed>
            <v-list-item>
                <v-list-item-content>
                    <v-list-item-title class="text-h5 text-uppercase font-weight-bold">
                        Chat App
                    </v-list-item-title>
                    <v-list-item-subtitle class="text-capitalize">
                        Connect with PEOPLE
                    </v-list-item-subtitle>
                </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>

            <v-list dense nav>
                <v-list-item
                    v-for="item in menuSingel"
                    :key="item.title"
                    link
                    @click="$vuetify.goTo(item.link, options)"
                >
                    <v-list-item-icon>
                        <v-icon>{{ item.icon }}</v-icon>
                    </v-list-item-icon>

                    <v-list-item-content>
                        <v-list-item-title class="text-uppercase">{{
                            item.title
                        }}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
            <v-list-item>
                <v-list-item-content>
                    <v-list-item-subtitle> Account </v-list-item-subtitle>
                </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>
            <v-list dense nav>
                <v-list-item
                    v-for="item in menuLoginExpand"
                    :key="item.title"
                    link
                    @click="$vuetify.goTo(item.link, options)"
                >
                    <v-list-item-icon>
                        <v-icon>{{ item.icon }}</v-icon>
                    </v-list-item-icon>

                    <v-list-item-content>
                        <v-list-item-title class="text-uppercase">
                            {{ item.title }}
                        </v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>
    </div>
</template>

<script>
import Scrollactive from "vue-scrollactive/src/scrollactive.vue";
import { mapGetters } from "vuex";
export default {
    name: "NavBar",

    components: {
        Scrollactive,
    },

    computed: {
        ...mapGetters({
            menuSingel: "getMenuSingel",
            menuLoginExpand: "getMenuLoginExpand",
        }),
        currentRouteName() {
            return this.$route.name;
        },
        options() {
            return {
                duration: this.duration,
                offset: this.offset,
                easing: this.easing,
            };
        },
    },

    watch: {
        offsetTop(newOffset) {
            this.appClass = newOffset ? "appG" : "";
        },
    },

    data: () => ({
        offsetTop: 0,
        drawer: false,

        duration: 1000,
        offset: 0,
        easing: "easeInOutCubic",

        appClass: "appT",
    }),

    methods: {
        onItemChanged() {
            console.log("tes");
        },
        onScroll(e) {
            if (typeof window === "undefined") return;
            const top = window.pageYOffset || e.target.scrollTop || 0;

            this.offsetTop = top > 10;
        },
    },
};
</script>

<style scoped>
#nav-bar {
    font-family: "Poppins", sans-serif;
}
.appT {
    background: transparent;
}
.appG {
    background: linear-gradient(to bottom right, #6c52e7, #9248dd);
}
.active {
    color: black;
}
.nav-title {
    color: white;
    text-transform: uppercase;
    font-size: 2em;
    font-weight: 900;
}
.nav-menu,
.scrollactive-item {
    font-size: 1em;
    font-weight: 600;
}
.nav-menu:hover {
    cursor: pointer;
}
.scrollactive-item {
    color: white;
    text-decoration: none;
    text-transform: uppercase;
}
.is-active {
    transition: 0.5s;
    color: #5d5f86;
    border: 1px solid #5d5f86;
    font-size: 2em;
    font-weight: 600;
}
</style>