<script>
import { mapGetters, mapActions } from "vuex";
export default {
    name: "Project",

    data() {
        return {
            selected: [],
            page: 1,
            perPage: 5,
        };
    },

    computed: {
        ...mapGetters({
            projectType: "getProjectType",
            project: "getProjectData",
        }),
        currentRouteName() {
            return this.$route.meta.title;
        },
        visiblePages() {
            return this.project.slice(
                (this.page - 1) * this.perPage,
                this.page * this.perPage
            );
        },
    },

    watch: {
        resetPage(newValue, oldValue) {
            if (newValue != oldValue) {
                this.resetPageTo();
            }
            console.log(newValue + " dari list mail");
        },
    },

    methods: {
        ...mapActions({
            setCurrentPage: "setCurrentPage",
        }),
        projectColor(q) {
            let data = this.projectType.find((value) => value.code == q);
            return data.bgColor;
        },
        readMail() {
            alert("sekarang page " + this.page);
        },
        resetPageTo() {
            this.page = 1;
            console.log("page = 1");
        },
        changeCurrentPage() {
            this.setCurrentPage(this.page);
        },
    },

    metaInfo() {
        return {
            // title: 'tes',
            // override the parent template and just use the above title only
            titleTemplate: "%s | " + this.currentRouteName,
            meta: [
                {
                    vmid: "title",
                    name: "title",
                    content: this.currentRouteName,
                },
                {
                    vmid: "description",
                    name: "description",
                    content: "bar",
                },
            ],
        };
    },
};
</script>

