<template>
    <div id="contact-us" class="base-page">
        <v-container
            class="d-flex flex-column justify-center align-center my-6"
        >
            <div class="d-flex flex-column justify-center align-center">
                <p
                    class="portofolio-one-page-text-color-3 portofolio-one-page-title-section text-h3 text-capitalize font-weight-bold"
                >
                    contact us
                </p>
                <p class="portofolio-one-page-text-color-2 text-body-1">
                    Lorem ipsum dolor sit amet consectetur adipisicing elit.
                </p>
            </div>
            <div class="">
                <v-form ref="form" v-model="valid">
                    <v-row class="d-flex justify-center">
                        <v-col cols="10" sm="10" md="6">
                            <v-text-field
                                v-model="name"
                                :rules="nameRules"
                                label="Name"
                                color="#6755e7"
                                outlined
                                required
                            ></v-text-field>
                        </v-col>
                        <v-col cols="10" sm="10" md="6">
                            <v-text-field
                                v-model="email"
                                :rules="emailRules"
                                label="E-mail"
                                outlined
                                required
                            ></v-text-field>
                        </v-col>
                        <v-col cols="10" sm="10">
                            <v-textarea
                                v-model="message"
                                :rules="messageRules"
                                outlined
                                name="input-7-4"
                                label="Your Message"
                            ></v-textarea>
                        </v-col>
                        <v-col
                            cols="10"
                            sm="10"
                            class="d-flex justify-center mt-n6"
                        >
                            <v-btn
                                class="my-4"
                                :color="primaryColor"
                                rounded
                                x-large
                                dark
                                @click="validate"
                            >
                                send message
                            </v-btn>
                        </v-col>
                        <v-col
                            cols="10"
                            sm="10"
                            class="d-flex justify-center mt-8"
                        >
                            <v-row>
                                <v-col
                                    cols="12"
                                    md="4"
                                    v-for="(q, i) in contactData"
                                    :key="i"
                                >
                                    <div
                                        class="d-flex flex-column justify-center align-center"
                                    >
                                        <v-icon color="#dee4e4" size="60">{{
                                            q.icon
                                        }}</v-icon>
                                        <span
                                            class="portofolio-one-page-text-color-3 text-capitalize text-h6 font-weight-bold my-2"
                                        >
                                            {{ q.title }}
                                        </span>
                                        <span
                                            class="portofolio-one-page-text-color-2 font-weight-bold text-body-1"
                                        >
                                            {{ q.content }}
                                        </span>
                                        <span
                                            class="portofolio-one-page-text-color-2 font-weight-bold text-body-1"
                                        >
                                            {{ q.content2 }}
                                        </span>
                                    </div>
                                </v-col>
                            </v-row>
                        </v-col>
                    </v-row>
                </v-form>
            </div>
        </v-container>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
    name: "ContactUs",
    data() {
        return {
            valid: true,
            name: "",
            nameRules: [
                (v) => !!v || "Name is required",
                (v) =>
                    (v && v.length <= 20) ||
                    "Name must be less than 20 characters",
            ],
            email: "",
            emailRules: [
                (v) => !!v || "E-mail is required",
                (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
            ],
            message: "",
            messageRules: [(v) => !!v || "Message is required"],
            contactData: [
                {
                    title: "location ",
                    icon: "mdi-map-marker",
                    content: "123 Business Avenue, Hoston, USA",
                    content2: "Suite 100-18, San Diego CA 2028",
                },
                {
                    title: "call us ",
                    icon: "mdi-phone",
                    content: "Phone: (+91) 123-456-7890",
                    content2: "Fax: (+060) 8898880089",
                },
                {
                    title: "email us ",
                    icon: "mdi-email-outline",
                    content: "support@gmail.com",
                    content2: "Mon to Fri (10 am - 8 pm)",
                },
            ],
        };
    },

    computed: {
        ...mapGetters({
            primaryColor: "getPrimaryColor",
        }),
    },

    methods: {
        validate() {
            // this.$refs.form.validate()
            this.$refs.form.reset();
            // this.$refs.form.resetValidation()
        },
    },
};
</script>

<style scoped></style>
